body {
  color: #000;
  margin: 0;
  font-family: helvetica, arial, sans-serif;
}

h1 {
  opacity: 0;
}

video {
  width: 100vw;
  height: 100vh;
}

button {
  cursor: pointer;
  color: #fff;
  pointer-events: all;
  background-color: #0060df;
  border: 1px solid #0060df;
  border-radius: 4px;
  padding: 10px 30px;
  transition: all .2s;
  position: absolute;
  top: 50vh;
  left: 50%;
  transform: translateX(-50%);
}

.removed {
  opacity: 0;
  pointer-events: none;
}

.invisible {
  color: #000;
  cursor: default;
  pointer-events: none;
  background: #fff;
  border: 1px solid #b1b1b3;
}

.videoView {
  float: center;
  width: 100%;
  cursor: pointer;
  margin: 2% 1%;
  position: relative;
}

.videoView p {
  float: center;
  color: #fff;
  z-index: 2;
  background-color: #ff6f00d9;
  border: 1px dashed #ffffffb3;
  margin: 0;
  padding: 5px;
  font-size: 25px;
  position: absolute;
}

.highlighter {
  z-index: 2;
  background: #00ff0040;
  border: 1px dashed #fff;
  place-content: center;
  display: grid;
  position: absolute;
}

.background {
  width: 100%;
  z-index: -1000;
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
}

